.formulario{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }
    label{
        font-weight: bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),textarea,select{
        padding: 1rem;
        display: block;
        width: 100%;
        background: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea{
        height: 20rem;
    }
    .form-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        input[type="radio"]{
            margin: 0;
            width: auto;
        }
    }
}


