    color: #FFFFFF; }

.no-webp .inicio {
  background-image: url(../img/header.jpg); }

.webp .inicio {
  background-image: url(../img/header.webp); }

.header {
  background: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .contain-header {
        min-height: 70rem; } }

.contain-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .contain-header h1 {
    text-align: left;
    color: #FFFFFF;
    font-weight: bold;
    line-height: 2;
    max-width: 40rem; }

.derecha {
  display: flex;
