  align-items: center; }
  @media (min-width: 768px) {
    .derecha {
      align-items: flex-end; } }
  .derecha .dark {
    width: 3rem;
    margin-top: 1rem;
    margin-bottom: 3rem;
    filter: invert(100%); }
    .derecha .dark:hover {
      cursor: pointer; }
    @media (min-width: 768px) {
      .derecha .dark {
        margin-top: 0; } }

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem; }
  @media (min-width: 768px) {
